@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.crypto-container {
    display: flex;
    justify-content: center;
}

.crypto-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.crypto {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.crypto h1 {
    font-size: 16px;
    width: 150px;
}

.crypto img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.crypto-symbol {
    text-transform: uppercase;
}

.crypto-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.crypto-price {
    width: 110px;
}

.crypto-mrktcap {
    width: 230px;
}

.crypto-volume {
    width: 155px;
}

.crypto-percent {
    width: 100%;
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    background-color: #1a1a1c;
    color: #fff;
}

.crypto-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
}

.crypto-search {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}

.crypto-text {
    margin-bottom: 32px;
    text-align: center;
}

.crypto-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(to left, #e4ccff, #ae72fd);
    color: #000000;
}

.crypto-input::-webkit-input-placeholder {
    color: #000000;
}

.crypto-input:-ms-input-placeholder {
    color: #000000;
}

.crypto-input::placeholder {
    color: #000000;
}
